// react-hamburger styling
// https://github.com/negomi/react-burger-menu

/* Position and sizing of burger button */
.bm-burger-button {
    display: none;
    position: fixed;
    width: 1.7rem;
    height: 1.5rem;
    left: 36px;
    top: 36px;
}
  
/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: #373a47;
}
  
/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 24px;
    width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
    background: #bdc3c7;
}

/* General sidebar styles */
.bm-menu {
    background: #373a47;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
    fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
    color: #b8b7ad;
    padding: 0.8em;
}

/* Individual item */
.bm-item {
    display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
    background: rgba(0, 0, 0, 0.3);
}


// Further customizations
// Use custom icon, and show it in tablets and below

.burger {
  position: absolute;
  font-size: 1rem;
  right: 1.2rem;
  top: 1rem;
  cursor: pointer;
  display: none !important;
}

@media (max-width: 640px) {
  .bm-burger-button {
    display: unset;
  }

  .burger {
    display: unset !important;
  }
}